/****************************************************************************************/
/* Client Specific Styling for C9 Work Specific Theme
/****************************************************************************************/

@import "client_variables";

.c9 dl,
.entry-content p,
.entry-footer,
p {
    line-height: $line-height-base;
}

a,
.editor-styles-wrapper .block-editor-rich-text__editable a {
    color: $link-color;
}

.entry-content p a {
    background: linear-gradient(0deg, rgba(74, 74, 74, 0.4) 12%, rgba(74, 74, 74, 0) 12%);
    text-decoration: none;
}

.entry-content p a:hover,
.entry-content p a:focus {
    background: linear-gradient(0deg, rgba(74, 74, 74, 0.6) 20%, rgba(74, 74, 74, 0) 20%);
}

.entry-content p a:focus {
    outline: 1px dotted $dark;
}

body.page {
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.8673844537815126) 1%, rgba(0, 0, 0, 0.667) 5%, rgba(0, 0, 0, 0) 13%);
    background-attachment: fixed;
    background-repeat: no-repeat;
}

a:hover {
    color: $link-hover-color;
}

.c9 .wrapper {
    margin-top: -115px;
    margin-bottom: 0px;
}

.single-post .wrapper,
.archive .c9 .wrapper {
    margin-top: 0px;
}

@media only screen and (min-width: 768px) {

    .blog .c9 .wrapper,
    .archive .c9 .wrapper {
        margin-top: 30px;
    }
}

.c9-layout-column-wrap {
    display: -ms-grid;
}

.c9-grid .c9-layout-column-wrap {
    position: relative;
}

@media only screen and (max-width: 667px) {
    .single .entry-content {
        padding: 20px;
    }

    .page-search-results {
        margin-top: 135px;
    }

    .blog .site .wrapper {
        margin-top: 30px;
    }
}

@media only screen and (min-width: 1200px) {
    .navbar .container {
        width: 1340px;
        max-width: 1340px;
    }

    #left-sidebar,
    #right-sidebar {
        background-color: $light;
    }
}

/****************************************************************************************/
/* Client default colors, button colors + background colors */
/****************************************************************************************/
.has-color-orange-color {
    color: $primary !important;
}

.has-color-yellow-color {
    color: $secondary !important;
}

.has-color-white-color,
.has-color-light-color {
    color: $light !important;
}

.has-color-black-color,
.has-color-dark-color {
    color: $dark !important;
}

.has-color-gray-color {
    color: $gray-600 !important;
}

.bg-primary,
.has-color-orange-background-color {
    background-color: $primary !important;
}

.bg-secondary,
.has-color-yellow-background-color {
    background-color: $secondary !important;
}

.has-color-gray-background-color {
    background-color: $gray-100 !important;
}

.has-color-white-background-color {
    background-color: $light !important;
}

.has-color-black-background-color {
    background-color: $dark !important;
}

.bg-success {
    background-color: $success !important;
}

.bg-info {
    background-color: $info !important;
}

.bg-warning {
    background-color: $warning !important;
}

.bg-danger {
    background-color: $danger !important;
}

.bg-dark {
    background-color: $dark !important;
}


mark.has-inline-color,
.mark.has-inline-color {
    background-color: inherit;
}

/****************************************************************************************/
/* Client buttons */
/****************************************************************************************/

.btn,
.btn:visited,
.entry-content .btn,
.entry-content .btn:visited,
.entry-content button,
.entry-content input[type="button"],
.entry-content input[type="reset"],
.entry-content input[type="submit"],
.entry-content .button,
.entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.wp-block-button__link,
.site .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.wp-block-button:not(.is-style-outline) .wp-block-button__link.mce-content-body,
.wp-block-file__button,
.wp-block-file .wp-block-file__button,
#searchsubmit,
.wp-block-button:not(.is-style-outline) .wp-block-button__link:not([href]):not([tabindex]) {
    padding: 19px 28px 14px 28px;
    color: $light;
    font-size: 2rem;
    letter-spacing: 0.01em;
    text-align: center;
    line-height: 1;
    border-radius: 0px;
    border: none;
    text-decoration: none;
}

.wp-block-button.is-style-outline .wp-block-button__link:not([href]):not([tabindex]),
.wp-block-button.is-style-outline .wp-block-button__link {
    background-image: none;
    background-color: transparent;
    border: 3px solid;
    padding: 16px 25px 11px 25px;
    font-size: 2rem;
    letter-spacing: 0.01em;
}

.c9-cta.c9-block-cta .wp-block-button__link.c9-button-shape-outline {
    border: 3px solid;
}

.c9 .entry-content .btn-secondary {
    background-color: $secondary;
}

.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:active,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:focus {
    color: $light;
    white-space: nowrap;
}

.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:visited,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:hover,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:not([href]):not([tabindex]) {
    color: $light;
}

.c9 button:hover,
.btn:hover,
.wp-block-button:not(.is-style-outline) .wp-block-button__link:hover,
.editor-styles-wrapper .button:hover,
.editor-styles-wrapper .button-primary:hover,
.editor-styles-wrapper .mce-content-body.button:hover,
.editor-styles-wrapper .wp-block-button:not(.is-style-outline) .wp-block-button__link:hover {
    opacity: 0.8;
    color: #fff;
}

.btn:hover,
.entry-content button:hover,
.wp-block-button a:hover,
.wp-block-button__link:hover {
    opacity: 0.8;
}

.where-to-buy>.btn:hover {
    color: $dark;
}

@media only screen and (max-width: 667px) {

    .c9 .c9-cta .wp-block-button__link.c9-button-size-small,
    .c9 .c9-cta .wp-block-button__link.c9-button-size-medium,
    .c9 .c9-cta .wp-block-button__link.c9-button-size-large,
    .c9 .c9-cta .wp-block-button__link.c9-button-size-extralarge {
        font-size: 2rem;
    }
}

/****************************************************************************************/
/* tags */
/****************************************************************************************/
.i-tag,
.tags-links,
.cat-links {
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-weight: normal;
    color: $link-hover-color;
    font-size: 1rem;
}

.tags-links a,
.cat-links a {
    text-decoration: none;
    color: $link-color;
    font-weight: $font-weight-bold;
}

.i-tag:before,
.tags-links a:before {
    display: inline;
    content: "\f02b";
    font: normal normal normal 14px/1 FontAwesome;
    color: $primary;
    padding-right: 2px;
}

.tags-links a:hover {
    color: $dark;
}

/****************************************************************************************/
/* Client navbar */
/****************************************************************************************/
#wrapper-navbar {
    z-index: 1020;
}

.header-navbar .navbar {
    background-color: transparent;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
    background-color: $dark;
}

.header-navbar .navbar.navbar-small {
    max-height: 55px;
    height: 55px;
    transition: opacity 0.4s ease-in-out;
    background-color: $light;
}

.navbar-small {
    max-height: 55px;
    height: 55px;
    transition: opacity 0.4s ease-in-out;
    background-color: $light;
}

.navbar-small .navbar-brand img {
    max-height: 45px;
    width: auto;
}

.navbar-nav li a,
.header-navbar .navbar-small .nav .nav-item>.nav-link {
    text-transform: uppercase;
    font-size: 1.5rem;
    line-height: 1;
    color: rgba(43, 43, 43, 0.77);
    letter-spacing: 1px;
}

.c9 .dropdown-toggle:after {
    border-top: none;
    border-right: none;
    border-bottom: none;
    border-left: none;
}

@media only screen and (min-width: 1px) {
    .header-navbar {
        height: 55px;
    }

    .navbar-brand img {
        width: auto;
        max-height: 45px;
        height: auto;
    }

    #page .navbar-small-mobile {
        padding-bottom: 0px;
    }

    .navbar-small .navbar-brand {
        background-size: contain;
        max-width: 285px;
        max-height: 45px;
        width: auto;
        height: auto;
        position: absolute;
    }

    .c9work-dark-logo {
        display: inline-block;
    }

    .navbar.navbar-small .c9work-dual-logo {
        display: none;
    }

    .navbar:not(.navbar-small) .c9work-dark-logo {
        display: none;
    }

    .navbar .navbar-brand {
        position: relative;
    }

    .navbar-small .navbar-brand img {
        opacity: 0;
    }

    .navbar .container-fluid .navbar-small-buttons {
        justify-content: flex-end;
    }

    .btn-nav-search,
    .navbar-toggler {
        color: $link-color;
    }

    .nav-toggle .navbar-toggler:focus,
    .nav-toggle .navbar-toggler:hover {
        color: $link-hover-color;
    }

    .navbar-toggler[aria-expanded=true] .fa-bars:before {
        content: '\f00d';
    }

    .nav-search,
    .nav-toggle {
        display: inline-block;
    }
}

@media only screen and (max-width: 667px) {
    .navbar .container-fluid {
        justify-content: space-between;
        flex-direction: row;
    }

    .navbar .container-fluid .c9-custom-logo {
        align-self: flex-start;
    }

    .navbar .container-fluid .navbar-small-buttons {
        margin-left: auto;
    }

    #mega-menu-wrap-primary {
        margin-left: 0px;
    }

    .btn-nav-search,
    .navbar-toggler {
        margin: 0px;
        padding: 5px;
    }

}

/* toggle adjustments for tablet when not using mega menu*/
@media only screen and (min-width: 668px) and (max-width: 991px) {
    .navbar-toggler {
        color: #fff;
    }

    .navbar:not(.navbar-small) .navbar-toggler:focus {
        color: #fff;
    }

    .navbar-small .btn-nav-search,
    .navbar-small .navbar-toggler {
        color: rgba(0, 0, 0, .5);
    }

}

@media only screen and (min-width: 768px) {
    .header-navbar {
        height: 108px;
    }
}

@media only screen and (min-width: 992px) {
    .navbar-brand img {
        max-width: 285px;
        max-height: 92px;
        width: auto;
        height: auto;
    }

    .c9 #mega-menu-wrap-primary {
        width: 100%;
        display: -ms-flex;
        display: flex;
        justify-content: center;
    }

    .navbar-nav,
    .c9 #mega-menu-wrap-primary #mega-menu-primary {
        display: -ms-flex;
        display: flex;
        justify-content: space-between;
    }

    .header-navbar {
        height: 115px;
    }

    .navbar-nav>li>a {
        color: #fff;
    }

}

/* navbar container widths for mega menu and normal menus*/
@media only screen and (min-width: 1px) {

    .navbar-nav,
    .c9 #mega-menu-wrap-primary #mega-menu-primary {
        width: 100%;
        margin-left: 0px;
    }
}

@media only screen and (min-width: 1px) and (max-width: 414px) {

    .navbar-nav,
    .c9 #mega-menu-wrap-primary #mega-menu-primary {
        top: 49px;
    }
}


/* spacing for that weird inbetween tablet size for navigation */
@media only screen and (min-width: 992px) and (max-width: 1024px) {

    .navbar-nav,
    .c9 #mega-menu-wrap-primary #mega-menu-primary {
        margin-left: 40px;
    }
}

@media only screen and (min-width: 1200px) {

    .navbar-nav,
    .c9 #mega-menu-wrap-primary #mega-menu-primary {
        width: 80%;
        margin-left: 0px;
    }
}

@media only screen and (max-width: 991px) {

    /* initial nav links should be white and then change to black after navbar small kicks in*/
    .navbar:not(.navbar-small) #mega-menu-wrap-primary .mega-menu-toggle .mega-toggle-block-1 .mega-toggle-label,
    .navbar:not(.navbar-small) #mega-menu-wrap-primary .mega-menu-toggle .mega-toggle-block-1:after {
        color: $light;
    }

    .header-navbar .navbar-collapse,
    .header-navbar .navbar-collapse.show {
        left: 0;
        background-color: $light;
    }

}

/****************************************************************************************/
/* Drop down menus, animations of menus, and button hover animations in main nav */
/****************************************************************************************/
.header-navbar .navbar .nav .nav-item .dropdown-item {
    padding: 10px 8px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item {
    margin: 0 6px;
}

@media only screen and (min-width: 992px) {
    .header-navbar .nav .nav-item .dropdown-menu {
        left: 50%;
        transform: translateX(-50%);
    }
}

.header-navbar .nav .nav-item .dropdown-menu {
    border-radius: 0px;
    margin-top: 1em;
    border: 0px;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu.show {
    display: block;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
}

@-webkit-keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

@keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

.navbar-small.fixed-top #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link:focus,
.navbar-small.fixed-top #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link:focus>span {
    color: $link-hover-color;
    outline: 1px dotted $link-hover-color;
}

#mega-menu-wrap-primary #mega-menu-primary a:hover,
#mega-menu-wrap-primary #mega-menu-primary a:focus {
    color: $link-hover-color;
}

#mega-menu-wrap-primary #mega-menu-primary a:focus {
    outline: 1px dotted $link-hover-color;
}

/* first level nav link hover effects */
.c9 .header-navbar:not(.navbar-small) .nav .nav-item>.nav-link,
.c9 .header-navbar:not(.navbar-small) .nav .nav-item .nav-link>span,
.navbar #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link,
.navbar #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link>span {
    display: inline-block;
    overflow: hidden;
    padding: 0px;

    &:before,
    &:after {
        right: 0;
        bottom: 0;
        background: darken($primary, 10%);
    }

    &:before {
        width: 100%;
        height: 2px;
        transform: translateX(-101%);
    }

    &:after {
        width: 2px;
        height: 100%;
        transform: translateY(101%);
    }

    >span {
        display: block;
        padding: 14px 12px;

        &:before,
        &:after {
            left: 0;
            top: 0;
            background: darken($primary, 10%);
        }

        &:before {
            width: 100%;
            height: 2px;
            transform: translateX(101%);
        }

        &:after {
            width: 2px;
            height: 100%;
            transform: translateY(-101%);
        }
    }

    &:focus:before,
    &:focus:after,
    &:focus>span:before,
    &:focus>span:after,
    &:hover:before,
    &:hover:after,
    &:hover>span:before,
    &:hover>span:after {
        transform: translate(0, 0);
        transition: transform 0.4s ease-in-out;
    }
}

@media only screen and (min-width: 414px) and (max-width: 991px) {

    /* nav link colors turn white inbetween mobile/tablet/laptop*/
    .header-navbar .nav .nav-item>.nav-link,
    .header-navbar .nav .nav-item .nav-link>span {
        color: $dark;
    }
}

.header-navbar .nav .nav-item>.nav-link,
.header-navbar .nav .nav-item .nav-link>span,
.navbar #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link,
.navbar #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link>span,
.navbar-small.fixed-top #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link,
.navbar-small.fixed-top #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link>span {
    position: relative;
    text-decoration: none;

    &:before,
    &:after {
        content: "";
        position: absolute;
    }
}

.header-navbar .navbar-small .nav .nav-item>.nav-link,
.header-navbar .navbar-small .nav .nav-item .nav-link>span {
    color: $dark;
}

@media only screen and (max-width: 768px) and (orientation: portrait) {
    .navbar:not(.navbar-small) #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link {
        overflow: visible;
    }

    .navbar:not(.navbar-small) #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item,
    .navbar-small #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link,
    .navbar-small.fixed-top #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link {
        margin: 0px auto;
        text-align: center;
        min-height: 40px;
    }

    .navbar:not(.navbar-small) #mega-menu-wrap-primary #mega-menu-primary>li.mega-menu-item>a.mega-menu-link>span {
        margin: 0px;
    }
}

/****************************************************************************************/
/* header navbar search customizations */
/****************************************************************************************/

.nav-search {
    margin-right: 10px;
}

@media only screen and (min-width: 992px) {

    .nav-search,
    .nav-toggle {
        line-height: 2;
    }

    .nav-search {
        display: none;
    }

    .header-navbar .search,
    .c9 #mega-menu-wrap-primary #mega-menu-primary li.search {
        min-width: 125px;
        display: flex;
        font-size: 1rem;
        align-items: center;
        justify-content: flex-start;
    }
}

.header-navbar .nav-search .btn-nav-search,
.header-navbar .navbar-toggler {
    font-size: 2rem;
}

.search form {
    display: inline;
}

.c9 .header-navbar .search input:focus {
    outline: 0;
}

.c9 .header-navbar .search:before,
.c9 #mega-menu-wrap-primary #mega-menu-primary li.search:before {
    display: inline-block;
    font-family: "FontAwesome";
    color: #fff;
    font-size: 1.9rem;
    content: "\f002";
}

.search ::-webkit-input-placeholder {
    color: #fff !important;
}

.search ::-moz-placeholder {
    color: #fff !important;
}

.search ::-ms-placeholder {
    color: #fff !important;
}

.search ::-webkit-input-placeholder {
    color: #fff !important;
}

.search :-ms-input-placeholder {
    color: #fff !important;
}

.search ::-ms-input-placeholder {
    color: #fff !important;
}

.search ::-moz-placeholder {
    color: #fff !important;
}

.search ::placeholder {
    color: #fff !important;
}

.header-navbar .search #s {
    background: transparent;
    border: none;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-bottom: 1px solid #fff;
    text-transform: uppercase;
    font-weight: 300;
    line-height: 1;
    width: 65px;
    display: inline-block;
    color: #fff;
    font-size: 1.9rem;
    margin-left: 4px;
    padding-bottom: 0px;
