.header-navbar .navbar-small .search ::-moz-placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::-webkit-input-placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::-ms-placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::-webkit-input-placeholder {
