    text-shadow: none;
}

.header-navbar .navbar-small .search :-ms-input-placeholder {
    color: rgb(158, 158, 158) !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::-ms-input-placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::-moz-placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search ::placeholder {
    color: $dark !important;
    text-shadow: none;
}

.header-navbar .navbar-small .search:before,
.header-navbar .navbar-small .search #s,
.header-navbar .navbar-small .search #s:focus {
    color: $dark;
    text-shadow: none;
}

.c9 .header-navbar .navbar-small .search:before,
.c9 .navbar-small #mega-menu-wrap-primary #mega-menu-primary li.search:before {
    color: $dark;
}

.header-navbar .navbar-small .search #s {
    border-bottom: 1px solid $dark;
}

@media only screen and (max-width: 991px) {

    .c9 .header-navbar li.search,
    .c9 #mega-menu-wrap-primary #mega-menu-primary li.search {
        display: none;
    }

    .header-navbar .navbar:not(.navbar-small) .nav-search .btn-nav-search {
        color: $light;
    }

    .header-navbar .navbar:not(.navbar-small) .search #s {
        border-bottom: 1px solid $dark;
        color: $dark;
    }

    .c9 .navbar:not(.navbar-small) #mega-menu-wrap-primary #mega-menu-primary li.search:before,
    .c9 .navbar:not(.navbar-small) li.search:before {
        color: $dark;
