    top: 0;
    bottom: 0;
    content: "";
    z-index: 7;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 1);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(37%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, 0)));
