/****************************************************************************************/
/* Utility Classes */
/****************************************************************************************/
.img-shadow,
.is-style-img-shadow,
:not([data-align])>.wp-block-image.is-style-img-shadow {
    position: relative;
    display: block;
}

.is-style-img-shadow img,
.img-shadow img {
    z-index: 10;
}

.is-style-img-shadow.wp-block-image:after,
.img-shadow.wp-block-image:after {
    position: relative;
    bottom: -30px;
    z-index: 8;
    content: "";
    display: inline-block;
    width: 100%;
    height: 30px;
    background-image: radial-gradient(ellipse, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 68%);
    background-repeat: no-repeat;
}