/****************************************************************************************/
/* Client Specific Styling for C9 Work Specific Theme
/****************************************************************************************/
:root {
  font-family: "helvetica neue", helvetica, sans-serif;
  --default-font: "helvetica neue", helvetica, sans-serif;
}

/*
Use the following with the build tools to generate new client styles for fonts
Remove inherit font settings on the $font-family variables

@import url("https://use.typekit.net/tet3dqk.css");
$headings-font-family: league-gothic;
$font-family-sans-serif: oswald;
$font-family-base: muli;

*/
.c9 dl,
.entry-content p,
.entry-footer,
p {
  line-height: 1.6;
}

a,
.editor-styles-wrapper .block-editor-rich-text__editable a {
  color: #5c5c5c;
}

.entry-content p a {
  background: linear-gradient(0deg, rgba(74, 74, 74, 0.4) 12%, rgba(74, 74, 74, 0) 12%);
  text-decoration: none;
}

.entry-content p a:hover,
.entry-content p a:focus {
  background: linear-gradient(0deg, rgba(74, 74, 74, 0.6) 20%, rgba(74, 74, 74, 0) 20%);
}

.entry-content p a:focus {
  outline: 1px dotted #000000;
}

body.page {
  background: black;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.8673844538) 1%, rgba(0, 0, 0, 0.667) 5%, rgba(0, 0, 0, 0) 13%);
  background-attachment: fixed;
  background-repeat: no-repeat;
}

a:hover {
  color: #000000;
}

.c9 .wrapper {
  margin-top: -115px;
  margin-bottom: 0px;
}

.single-post .wrapper,
.archive .c9 .wrapper {
  margin-top: 0px;
}

@media only screen and (min-width: 768px) {
  .blog .c9 .wrapper,
.archive .c9 .wrapper {
    margin-top: 30px;
  }
}
.c9-layout-column-wrap {
  display: -ms-grid;
}

.c9-grid .c9-layout-column-wrap {
  position: relative;
}

@media only screen and (max-width: 667px) {
  .single .entry-content {
    padding: 20px;
  }

  .page-search-results {
    margin-top: 135px;
  }

  .blog .site .wrapper {
    margin-top: 30px;
  }
}
@media only screen and (min-width: 1200px) {
  .navbar .container {
    width: 1340px;
    max-width: 1340px;
  }

  #left-sidebar,
#right-sidebar {
    background-color: #ffffff;
  }
}
/****************************************************************************************/
/* Client default colors, button colors + background colors */
/****************************************************************************************/
.has-color-orange-color {
  color: #FCBD13 !important;
}

.has-color-yellow-color {
  color: #FEC50A !important;
}

.has-color-white-color,
.has-color-light-color {
  color: #ffffff !important;
}

.has-color-black-color,
.has-color-dark-color {
  color: #000000 !important;
}

.has-color-gray-color {
  color: #979797 !important;
}

.bg-primary,
.has-color-orange-background-color {
  background-color: #FCBD13 !important;
}

.bg-secondary,
.has-color-yellow-background-color {
  background-color: #FEC50A !important;
}

.has-color-gray-background-color {
  background-color: #ADB1B2 !important;
}

.has-color-white-background-color {
  background-color: #ffffff !important;
}

.has-color-black-background-color {
  background-color: #000000 !important;
}

.bg-success {
  background-color: #21a77a !important;
}

.bg-info {
  background-color: #f7f7f9 !important;
}

.bg-warning {
  background-color: #ec971f !important;
}

.bg-danger {
  background-color: #843534 !important;
}

.bg-dark {
  background-color: #000000 !important;
}

mark.has-inline-color,
.mark.has-inline-color {
  background-color: inherit;
}

/****************************************************************************************/
/* Client buttons */
/****************************************************************************************/
.btn,
.btn:visited,
.entry-content .btn,
.entry-content .btn:visited,
.entry-content button,
.entry-content input[type=button],
.entry-content input[type=reset],
.entry-content input[type=submit],
.entry-content .button,
.entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.wp-block-button__link,
.site .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.wp-block-button:not(.is-style-outline) .wp-block-button__link.mce-content-body,
.wp-block-file__button,
.wp-block-file .wp-block-file__button,
#searchsubmit,
.wp-block-button:not(.is-style-outline) .wp-block-button__link:not([href]):not([tabindex]) {
  padding: 19px 28px 14px 28px;
  color: #ffffff;
  font-size: 2rem;
  letter-spacing: 0.01em;
  text-align: center;
  line-height: 1;
  border-radius: 0px;
  border: none;
  text-decoration: none;
}

.wp-block-button.is-style-outline .wp-block-button__link:not([href]):not([tabindex]),
.wp-block-button.is-style-outline .wp-block-button__link {
  background-image: none;
  background-color: transparent;
  border: 3px solid;
  padding: 16px 25px 11px 25px;
  font-size: 2rem;
  letter-spacing: 0.01em;
}

.c9-cta.c9-block-cta .wp-block-button__link.c9-button-shape-outline {
  border: 3px solid;
}

.c9 .entry-content .btn-secondary {
  background-color: #FEC50A;
}

.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:active,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:focus {
  color: #ffffff;
  white-space: nowrap;
}

.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:visited,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:hover,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.c9 .entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link:not([href]):not([tabindex]) {
  color: #ffffff;
}

.c9 button:hover,
.btn:hover,
.wp-block-button:not(.is-style-outline) .wp-block-button__link:hover,
.editor-styles-wrapper .button:hover,
.editor-styles-wrapper .button-primary:hover,
.editor-styles-wrapper .mce-content-body.button:hover,
.editor-styles-wrapper .wp-block-button:not(.is-style-outline) .wp-block-button__link:hover {
  opacity: 0.8;
  color: #fff;
}

.btn:hover,
.entry-content button:hover,
.wp-block-button a:hover,
.wp-block-button__link:hover {
  opacity: 0.8;
}

.where-to-buy > .btn:hover {
  color: #000000;
}

@media only screen and (max-width: 667px) {
  .c9 .c9-cta .wp-block-button__link.c9-button-size-small,
.c9 .c9-cta .wp-block-button__link.c9-button-size-medium,
.c9 .c9-cta .wp-block-button__link.c9-button-size-large,
.c9 .c9-cta .wp-block-button__link.c9-button-size-extralarge {
    font-size: 2rem;
  }
}
/****************************************************************************************/
/* tags */
/****************************************************************************************/
.i-tag,
.tags-links,
.cat-links {
  letter-spacing: 0.01em;
  text-transform: uppercase;
  font-weight: normal;
  color: #000000;
  font-size: 1rem;
}

.tags-links a,
.cat-links a {
  text-decoration: none;
  color: #5c5c5c;
  font-weight: 600;
}

.i-tag:before,
.tags-links a:before {
  display: inline;
  content: "\f02b";
  font: normal normal normal 14px/1 FontAwesome;
  color: #FCBD13;
  padding-right: 2px;
}

.tags-links a:hover {
  color: #000000;
}

/****************************************************************************************/
/* Client navbar */
/****************************************************************************************/
#wrapper-navbar {
  z-index: 1020;
}

.header-navbar .navbar {
  background-color: transparent;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
  background-color: #000000;
}

.header-navbar .navbar.navbar-small {
  max-height: 55px;
  height: 55px;
  transition: opacity 0.4s ease-in-out;
  background-color: #ffffff;
}

.navbar-small {
  max-height: 55px;
  height: 55px;
  transition: opacity 0.4s ease-in-out;
  background-color: #ffffff;
}

.navbar-small .navbar-brand img {
  max-height: 45px;
  width: auto;
}

.navbar-nav li a,
.header-navbar .navbar-small .nav .nav-item > .nav-link {
  text-transform: uppercase;
  font-size: 1.5rem;
  line-height: 1;
  color: rgba(43, 43, 43, 0.77);
  letter-spacing: 1px;
}

.c9 .dropdown-toggle:after {
  border-top: none;
  border-right: none;
  border-bottom: none;
  border-left: none;
}

@media only screen and (min-width: 1px) {
  .header-navbar {
    height: 55px;
  }

  .navbar-brand img {
    width: auto;
    max-height: 45px;
    height: auto;
  }

  #page .navbar-small-mobile {
    padding-bottom: 0px;
  }

  .navbar-small .navbar-brand {
    background-size: contain;
    max-width: 285px;
    max-height: 45px;
    width: auto;
    height: auto;
    position: absolute;
  }

  .c9work-dark-logo {
    display: inline-block;
  }

  .navbar.navbar-small .c9work-dual-logo {
    display: none;
  }

  .navbar:not(.navbar-small) .c9work-dark-logo {
    display: none;
  }

  .navbar .navbar-brand {
    position: relative;
  }

  .navbar-small .navbar-brand img {
    opacity: 0;
  }

  .navbar .container-fluid .navbar-small-buttons {
